.glow {
  --r: 0;
  --x: 50%;
  --y: 50%;
  background-image: radial-gradient(var(--r) var(--r) at var(--x) var(--y), var(--glow), transparent);
}
.glow:hover {
  --r: 60px;
}
@media (any-pointer: coarse) {
  .glow:hover {
    --r: 90px;
  }
}
@media (any-hover: none) {
  .glow:hover {
    --r: 0 !important;
  }
}
