* {
  margin: 0;
  padding: 0;
  border: none;
  box-sizing: border-box;
  font-family: main, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
}
*::selection {
  text-shadow: initial;
  background-color: #000;
  color: #fff;
}
html,
body,
#root {
  width: 100%;
}
body {
  color: #fff;
  background-color: #292929;
}
:root.menu body {
  overflow-y: hidden;
}
:root.light body {
  color: #000;
  background-color: #fff;
}
@media print {
  body {
    color: #000;
    background-color: #fff;
  }
}
a {
  color: #ff72aa;
}
:root.light a {
  color: #9c0056;
}
@media print {
  a {
    color: #9c0056;
  }
}
a:focus {
  outline: none;
  border-radius: 3px;
}
:root.keyboard a:focus {
  box-shadow: 0 0 0 2px #9c0056;
}
:root.light:root.keyboard a:focus {
  box-shadow: 0 0 0 2px #ff72aa;
}
@media print {
  :root.keyboard a:focus {
    box-shadow: 0 0 0 2px #ff72aa;
  }
}
